// import {uniIndexedList} from '@dcloudio/uni-ui'
var app = getApp().globalData
export default {
	// components: {uniIndexedList},
	data() {
		return {
			hotList:[],
			phoneList:[],
			toIndex:''
			
		}
	},
	
	onShow() {
		uni.showLoading({
		    title: '加载中'
		});
		this.$api.phone({}).then(res=>{
			this.hotList = res.data.data.hot
			this.phoneList = res.data.data.list
			uni.hideLoading();
		})
	},
	methods: {
	 hot(e){
		 // console.log(e.currentTarget.id)
		 this.toIndex = e.currentTarget.id
		 // console.log(typeof this.toIndex)
	 },
	 scroll(e){
		 this.toIndex = ''
	 },
	 phone(e){
		 // console.log(e)
		 this.toIndex = e
	 },
	 // 改变号码前缀存到 全局中
	  changeHot(code){
		 app.num = code
		 uni.setStorageSync('phoneCode',code)
		 // console.log(uni.getStorageSync('phoneCode'))
		 uni.navigateBack({
		 	delta: 1
		 })
	 },
	 changeCode(code){
		 app.num = code
		 uni.setStorageSync('phoneCode',code)
		 // console.log(uni.getStorageSync('phoneCode'))
		 uni.navigateBack({
			delta: 1
		 })
	 }
	
},

}
